import React, { } from 'react';
import { Tabs, Input, Divider } from "antd";
import { ConfigPanel } from './ConfigPanel';
import { autoJustSizeOfSubRequirement, getSelectedCell } from '../utils/Util';
import { Group } from '../nodes/Group';

const { TabPane } = Tabs;
const { TextArea } = Input

export interface Props {
  onRef: (c: ConfigGroup) => void
}

export interface State {
  subrequirement: string
}

export class ConfigGroup extends React.Component<Props, State> {
  state: State = {
    subrequirement: ""
  }
  public parent: ConfigPanel

  setParent(c: ConfigPanel) {
    this.parent = c
  }

  componentDidMount() {
    this.props.onRef(this)
  }

  onChangeSubRequirement = (e: any) => {
    const cell = getSelectedCell(this.parent.parent.graph)
    if (cell !== null) {
      const node = (cell as Group)
      node.setAttrByPath('label/text', e.target.value)
      this.setState({ subrequirement: e.target.value })
      autoJustSizeOfSubRequirement(node)
    }
  }

  render() {
    return (
      <Tabs defaultActiveKey="1">
        <TabPane tab="Requirements Module Setting" key="1" style={{ padding: 3 }}  >
          <Divider orientation="left" orientationMargin="0">Requirements Description</Divider>
          <TextArea rows={10} autoFocus={true} onChange={this.onChangeSubRequirement} value={this.state.subrequirement} placeholder="Please input the requirements description"/>
        </TabPane>
      </Tabs>
    );
  }
}
