import React, { } from 'react';
import { Tabs, Input, Form, Divider } from "antd";
import { ConfigPanel } from './ConfigPanel';
import { Node } from '@antv/x6'
import { autoJustSizeOfSubQuery, getSelectedCell } from '../utils/Util';

export interface Props {
  onRef: (c: ConfigNode) => void
}

export interface State {
  subqueryText: string
  filterText: string
  showText: string
}

const { TabPane } = Tabs;
const { Item } = Form;
const { TextArea } = Input

export class ConfigNode extends React.Component<Props, State> {
  state: State = {
    subqueryText: '',
    filterText: '',
    showText: ''
  }

  public parent: ConfigPanel

  setParent(c: ConfigPanel) {
    this.parent = c
  }

  componentDidMount() {
    this.props.onRef(this)
  }
  componentWillUnmount = () => {
    this.setState = (state,callback)=>{
      return;
    };
}

  onChangeSubQuery = (e: any) => {
    const cell = getSelectedCell(this.parent.parent.graph)
    if (cell !== null) {
      const node = (cell as Node)
      node.setAttrByPath('rank/text', e.target.value)
      this.setState({ subqueryText: e.target.value })
      autoJustSizeOfSubQuery(node)
    }
  }

  onChangeFilter = (e: any) => {
    const cell = getSelectedCell(this.parent.parent.graph)
    if (cell !== null) {
      const node = (cell as Node)
      node.setAttrByPath('name/text', e.target.value)
      this.setState({ filterText: e.target.value })
      autoJustSizeOfSubQuery(node)
    }
  }

  onChangeShow = (e: any) => {
    const cell = getSelectedCell(this.parent.parent.graph)
    if (cell !== null) {
      const node = (cell as Node)
      node.setAttrByPath('show/text', e.target.value)
      this.setState({ showText: e.target.value })
      autoJustSizeOfSubQuery(node)
    }
  }

  render() {
    return (
      <Tabs defaultActiveKey="1">
        <TabPane tab="Mini-query Setting" key="1" style={{ padding: 3 }}  >
          <Divider orientation="left" orientationMargin="0">Table</Divider>
          <TextArea rows={1} autoFocus={true} onChange={this.onChangeSubQuery} value={this.state.subqueryText} placeholder="Table name"/>
          <Item label=""></Item>
          <Divider orientation="left" orientationMargin="0">Filter</Divider>
          <TextArea rows={5} onChange={this.onChangeFilter} value={this.state.filterText} placeholder="Conditions to filter rows"/>
          <Item label=""></Item>
          <Divider orientation="left" orientationMargin="0">Columns</Divider>
          <TextArea rows={10} onChange={this.onChangeShow} value={this.state.showText} placeholder="Columns to display"/>
        </TabPane>
      </Tabs>
    );
  }
}