import React from 'react';
import { ConfigNode } from './ConfigNode';
import { ConfigGraph } from './ConfigGraph';
import { ConfigEdge } from './ConfigEdge';
import { ConfigGroup } from './ConfigGroup';
import App from '../App'
import './ConfigPanel.css'
import { ConfigReuse } from './ConfigReuse';

export interface Props {
  onRef: (c: ConfigPanel) => void
}

export interface State {
  editType: string
}

export class ConfigPanel extends React.Component<Props, State> {
  state: State = {
    editType: "GRAPH"
  }
  public parent: App
  public nodePanel: ConfigNode
  public edgePanel: ConfigEdge
  public groupPanel: ConfigGroup
  public reusePanel: ConfigReuse
  public graphPanel: ConfigGraph;

  componentDidMount() {
    this.props.onRef(this)
  }

  setParent(c: App) {
    this.parent = c
  }

  onBindNodePanel = (c: ConfigNode) => {
    this.nodePanel = c
    this.nodePanel.setParent(this)
  }

  onBindEdgePanel = (c: ConfigEdge) => {
    this.edgePanel = c
    this.edgePanel.setParent(this)
  }
  onBindGraphPanel = (c: ConfigGraph) => {
    this.graphPanel = c
    this.graphPanel.setParent(this)
  }

  onBindGroupPanel = (c: ConfigGroup) => {
    this.groupPanel = c
    this.groupPanel.setParent(this)
  }
  onBindReusePanel = (c: ConfigReuse) => {
    this.reusePanel = c
    this.reusePanel.setParent(this)
  }

  render() {
    return (
      <div className="configPanel">
        {this.state.editType === 'GRAPH' && <ConfigGraph onRef={this.onBindGraphPanel}/>}
        {this.state.editType === 'NODE' && <ConfigNode onRef={this.onBindNodePanel} />}
        {this.state.editType === 'GROUP' && <ConfigGroup onRef={this.onBindGroupPanel} />}
        {this.state.editType === 'EDGE' && <ConfigEdge onRef={this.onBindEdgePanel} />}
        {this.state.editType === 'REUSE' && <ConfigReuse onRef={this.onBindReusePanel} />}
      </div>
    );
  }
}
