import { Graph } from '@antv/x6'
import { Node } from '@antv/x6'
import { Cell } from '@antv/x6'
import { Edge } from '@antv/x6'
import { Model } from '@antv/x6'
import { Group } from '../nodes/Group'
import { showOrHidePorts, addCellToGroup, autoJustSizeOfSubRequirement } from './Util'
import { ConfigPanel } from '../configs/ConfigPanel'

export function expand(node: Node) {
    const parent = node.getParent()
    if (parent && parent.isNode()) {
        let originSize = parent.getSize()
        let originPosition = parent.getPosition()
        let x = originPosition.x
        let y = originPosition.y
        let cornerX = originPosition.x + originSize.width
        let cornerY = originPosition.y + originSize.height

        let hasChange = false
        const child = node
        const bbox = child.getBBox().inflate(10)
        bbox.y = bbox.y - 20
        bbox.height = bbox.height + 20
        const corner = bbox.getCorner()
        if (bbox.x < x) { x = bbox.x; hasChange = true; }
        if (bbox.y < y) { y = bbox.y; hasChange = true; }
        if (corner.x > cornerX) { cornerX = corner.x; hasChange = true; }
        if (corner.y > cornerY) { cornerY = corner.y; hasChange = true; }

        if (hasChange) {
            if (parent instanceof Group) {
                let group = (parent as Group)
                if (!group.isCollapsed()) {
                    parent.setPosition(x, y)
                    parent.setSize(cornerX - x, cornerY - y)
                    expand(parent)
                }
            }
        }
    }
}

function clickEdge(graph: Graph, child: ConfigPanel, edge: Edge) {
    graph.unselect(graph.getCells())
    graph.select(edge)
    child.setState({ editType: 'GRAPH' })
    child.setState({ editType: 'EDGE' })
    const lables = edge.getLabels()
    if (lables.length > 0) {
        const label = edge.getLabels()[0]
        const text: string = label!.attrs!.label!.text!.toString()
        child.edgePanel.setState({ relationText: text })
    } else {
        child.edgePanel.setState({ relationText: "" })
    }
}
function clickNode(graph: Graph, child: ConfigPanel, node: Node) {
    graph.unselect(graph.getCells())
    graph.select(node)
    child.setState({ editType: 'GRAPH' })
    if (node instanceof Group) {
        child.setState({ editType: 'GROUP' })
        child.groupPanel.setState({ subrequirement: node.getAttrByPath('label/text') })
    } else if (node.shape === 'reuse-node') {
        child.setState({ editType: 'REUSE' })
        child.reusePanel.setState({ subqueryText: node.getAttrByPath('name/text') })
    } else if (node.shape === 'subquery-node') {
        child.setState({ editType: 'NODE' })
        child.nodePanel.setState({ subqueryText: node.getAttrByPath('rank/text') })
        child.nodePanel.setState({ filterText: node.getAttrByPath('name/text') })
        child.nodePanel.setState({ showText: node.getAttrByPath('show/text') })
    }
}
export function registerEvents(graph: Graph, child: ConfigPanel) {
    graph.on('edge:selected', (args: {
        cell: Cell
        edge: Edge
        options: Model.SetOptions
    }) => {
        args.edge.setAttrByPath('line/stroke', '#F87217')
        args.edge.setAttrByPath('line/strokeDasharray', 3)
    })
    graph.on('edge:unselected', (args: {
        cell: Cell
        edge: Edge
        options: Model.SetOptions
    }) => {
        args.edge.setAttrByPath('line/stroke', '#000000')
        args.edge.setAttrByPath('line/strokeDasharray', 0)
    })
    graph.on('node:selected', (args: {
        cell: Cell
        node: Node
        options: Model.SetOptions
    }) => {
        if (args.node instanceof Group) {
            if ((args.node as Group).isCollapsed()) {
                return
            }
        }
        let pref = ''
        if (args.node instanceof Group) { pref = 'rect' }
        else { pref = 'body' }
        args.node.setAttrByPath(pref + '/stroke', '#F87217')
        args.node.setAttrByPath(pref + '/strokeDasharray', 3)
        // const cells = args.node.getChildren()
        // if (cells) {
        //     cells.forEach((element) => {
        //         graph.select(element)
        //     })
        // }
    })
    graph.on('node:unselected', (args: {
        cell: Cell
        node: Node
        options: Model.SetOptions
    }) => {
        let pref = ''
        if (args.node instanceof Group) { pref = 'rect' }
        else { pref = 'body' }
        args.node.setAttrByPath(pref + '/stroke', '#000000')
        args.node.setAttrByPath(pref + '/strokeDasharray', 0)
    })
    graph.on('edge:added', ({ edge }) => {
        clickEdge(graph, child, edge)
    })

    graph.on('node:added', ({ node }) => {
        console.log('point: ')
        clickNode(graph, child, node)
        const { x, y } = node.position()
        addCellToGroup(x, y, node, graph)
    })

    graph.on('node:click', ({ node }) => {
        clickNode(graph, child, node)
    })
    graph.on('blank:click', ({e, x, y}) => {
        graph.unselect(graph.getCells())
    })
    graph.on('edge:click', ({ edge }) => {
        clickEdge(graph, child, edge)
    })

    graph.on('node:mouseenter', () => {
        showOrHidePorts(true)
    })

    graph.on('node:mouseleave', () => {
        showOrHidePorts(false)
    })

    graph.on('node:mouseup', ({ e, x, y, node }) => {
        addCellToGroup(x, y, node, graph)
    })

    graph.on('blank:mouseup', ({ e, x, y }) => {
        child.setState({ editType: 'GRAPH' })
    })

    graph.on('node:change:size', ({ node }) => {
        expand(node)
    })
    graph.on('node:change:position', ({ node }) => {
        expand(node)
    })

    graph.on('node:collapse', ({ node }: { node: Group }) => {
        node.toggleCollapse()
        autoJustSizeOfSubRequirement(node)

        const collapse = (parent: Group) => {
            const cells = parent.getChildren()
            if (cells) {
                cells.forEach((cell) => {
                    if (node.isCollapsed()) {
                        cell.hide()
                    } else {
                        cell.show()
                    }

                    if (cell instanceof Group) {
                        if (!cell.isCollapsed()) {
                            collapse(cell)
                        }
                    }
                })
            }
            // const allEdges = graph.getEdges()
            // allEdges.forEach((edge) => {
            //     const startNode = edge.getSourceNode();
            //     const endNode = edge.getTargetNode();
            // if (startNode instanceof Group || endNode instanceof Group) {
            //     graph.removeEdge(edge)
            // }
            //     if (parent.contains(startNode) && parent.contains(endNode)) {
            //     } else if (parent.contains(startNode)) {
            //         createEdge('' + parent.id + '' + endNode!.id, parent.id, endNode!.id, graph)
            //     } else if (parent.contains(endNode)) {
            //         createEdge('' + startNode!.id + '' + parent.id, startNode!.id, parent.id, graph)
            //     } else {
            //     }
            // })
        }

        collapse(node)
    })
}
