import React, { } from 'react';
import { Edge } from '@antv/x6'
import { Tabs, Divider, Input } from "antd";
import { ConfigPanel } from './ConfigPanel';

export interface Props {
  onRef: (c: ConfigEdge) => void
}

export interface State {
  relationText: string
}

const { TabPane } = Tabs;
const { TextArea } = Input;

export class ConfigEdge extends React.Component<Props, State> {
  state: State = {
    relationText: ""
  }
  public parent: ConfigPanel

  setParent(c: ConfigPanel) {
    this.parent = c
  }

  componentDidMount() {
    this.props.onRef(this)
  }

  onChangeRelation = (e: any) => {
    const cell = this.parent.parent.graph.getSelectedCells()[0]
    const node = (cell as Edge)
    node.setLabels(e.target.value)
    this.setState({relationText: e.target.value})
  }

  render() {
    return (
      <Tabs defaultActiveKey="1">
        <TabPane tab="Relation Setting" key="1" style={{ padding: 3 }}  >
          <Divider orientation="left" orientationMargin="0">Relation</Divider>
          <TextArea rows={10} autoFocus={true} onChange={this.onChangeRelation} value={this.state.relationText} placeholder="EXPAND / REF / UNION"/>
        </TabPane>
      </Tabs>
    );
  }
}
