import React from 'react';
import { Button, Divider, Tabs } from "antd";
// import { Image } from "antd";
import { Timeline } from 'antd';
// import TextArea from 'antd/lib/input/TextArea';
import { ConfigPanel } from './ConfigPanel';
import { Upload } from 'antd';
import { message } from 'antd';
import { UploadOutlined } from '@ant-design/icons';
const { TabPane } = Tabs;
export interface Props {
  onRef: (c: ConfigGraph) => void
}

export class ConfigGraph extends React.Component<Props> {
  public parent: ConfigPanel
  
  componentDidMount() {
    this.props.onRef(this)
  }

  setParent(c: ConfigPanel) {
    this.parent = c
  }
  draw = () => {
    const json = document.getElementById('jsonText')?.textContent as string
    this.parent.parent.graph.fromJSON(JSON.parse(json))
  }

  render() {
    const that = this
    const uploadJSON = {
      beforeUpload(file: Blob) {
          const reader = new FileReader();
          reader.readAsText(file)
          reader.onload = () => {
            that.parent.parent.graph.fromJSON(JSON.parse(reader.result as string))
            message.success('Upload successfully')
            return true
          };
          return false
      },
    }
    return (
      <div>
        <Tabs defaultActiveKey="1">
          <TabPane tab="Introduction" key="1" style={{ padding: 3 }}  >
            {/* <Divider orientation="left" orientationMargin="0"></Divider> */}
            <Timeline style={{ marginTop: 30 }}>
              <Timeline.Item><strong>VQFLow</strong> is the abbreviation for <strong>V</strong>isual <strong>Q</strong>uery <strong>F</strong>low, which aims to help business analysts quickly and clearly describe the requirements of complex queries.</Timeline.Item>
              <Timeline.Item>The query requirements described by VQFlow are easy to understand, and thus developers can quickly and correctly write complex queries.</Timeline.Item>
              {/* <Timeline.Item>VQFlow can improve the understandability of the existing select queries, and thus maintainers can quickly easily modifiy the select queries.</Timeline.Item> */}
              {/* <Timeline.Item>Demo: https://github.com/vqflow/questionnaire/blob/main/demo.mp4</Timeline.Item> */}
              {/* <Timeline.Item>Samples: https://github.com/vqflow/questionnaire/blob/main/Three_cases.zip</Timeline.Item> */}
              {/* <Timeline.Item>Logo: <Image src="big_logo.png" /></Timeline.Item> */}
            </Timeline>
          </TabPane>
          {/* <TabPane tab='JSON' key="2" style={{ padding: 3 }}>
            <Divider orientation="left" orientationMargin="0">Draw from JSON data</Divider>
            <TextArea id="jsonText" rows={20} autoFocus={true} placeholder="Please input JSON data" />

            <Button type='primary' block onClick={this.draw} style={{ marginTop: 10 }}>Draw</Button>
          </TabPane> */}
          <TabPane tab='Upload JSON' key="3" style={{ padding: 3 }}>
            <Divider orientation="left" orientationMargin="0">Draw from JSON data</Divider>
            <Upload {...uploadJSON}>
              <Button icon={<UploadOutlined />}>Click to Upload</Button>
            </Upload>
          </TabPane>
        </Tabs>
      </div>

    );
  }
}
