export default class App extends React.Component {
  public container: HTMLDivElement
  public stencilContainer: HTMLDivElement
  public graph: Graph
  public child: ConfigPanel

  componentDidMount() {
    this.child.setParent(this)
    this.graph = new Graph({
      keyboard: {
        enabled: true,
      },
      history: true,
      container: this.container,
      // grid: true,
      resizing: {
        enabled: true,
        allowReverse: false,
      },
      clipboard: {
        enabled: true,
        useLocalStorage: true,
      },
      connecting: {
        snap: true,
        highlight: true,
        allowBlank: false,
        allowLoop: false,
        allowEdge: false,
        allowMulti: false,
        allowNode: false,
        allowPort: true,
      },
      highlighting: {
        embedding: {
          name: 'stroke',
          args: {
            padding: -1,
            attrs: {
              stroke: '#73d13d',
            },
          },
        },
      },
      snapline: { enabled: true, sharp: true, },
      selecting: { enabled: true, },
      scroller: {
        enabled: true,
        pageVisible: false,
        pageBreak: false,
        pannable: true,
      }
    })

    this.graph.bindKey('alt+shift+b', this.onClickBack)
    this.graph.bindKey('alt+shift+f', this.onClickFront)

    this.graph.bindKey('alt+shift+=', this.onClickZoomIn)
    this.graph.bindKey('alt+shift+-', this.onClickZoomOut)

    this.graph.bindKey('delete', this.onClickDelete)
    this.graph.bindKey('meta+c', this.onClickCopy)
    this.graph.bindKey('ctrl+c', this.onClickCopy)
    this.graph.bindKey('meta+v', this.onClickPaste)
    this.graph.bindKey('ctrl+v', this.onClickPaste)

    this.graph.bindKey('meta+z', this.onClickUndo)
    this.graph.bindKey('ctrl+z', this.onClickUndo)
    this.graph.bindKey('meta+shift+z', this.onClickRedo)
    this.graph.bindKey('ctrl+shift+z', this.onClickRedo)

    this.graph.bindKey('alt+shift+s', this.onClickToJPG)
    this.graph.bindKey('alt+shift+c', this.onClickCenter)
    this.graph.bindKey('alt+shift+a', this.onClickAdjust)

    this.registerSider()
    this.graph.zoomToFit({ padding: 10, maxScale: 1 })
    this.graph.centerContent()
    hidePorts(false)
    registerEvents(this.graph, this.child)
    this.graph.unselect(this.graph.getCells())
    // this.graph.fromJSON()
  }

  registerSider = () => {
    const slider = new Stencil({
      title: '',
      target: this.graph,
      stencilGraphWidth: Math.floor(window.innerWidth * 0.12),
      stencilGraphHeight: 700,
      stencilGraphPadding: 0,
      // stencilGraphOptions: {},
      layoutOptions: {
        columns: 1,
        marginY: 20,
        // resizeToFit: true,
        marginX: Math.floor(window.innerWidth * 0.01),
        dx: 0,
        dy: 0,
        center: true,
        rowHeight: 95,
        columnWidth: Math.floor(window.innerWidth * 0.10),
      },
      groups: [
        {
          name: 'group1',
          title: 'VQFlow',
          collapsable: false,
        },
      ],
      getDropNode(draggingNode, options) {
        const dropNode = draggingNode.clone()
        dropNode.setZIndex(1)
        return dropNode
      },
      getDragNode(node) {
        node = node.clone()
        if (node instanceof Group) {
          node.setAttrByPath('label/text', '')
          node.setSize({ width: 190, height: 108 })
        } else if (node.shape === 'subquery-node') {
          node.setAttrByPath('rank/text', '')
          node.setAttrByPath('name/text', '')
          node.setAttrByPath('show/text', '')
          node.setSize({ width: 150, height: 78 })
        } else if (node.shape === 'reuse-node') {
          node.setAttrByPath('rank/text', 'REUSE')
          node.setAttrByPath('name/text', '')
          node.setSize({ width: 190, height: 58 })
        }
        return node
      },
    })

    this.graph.cleanClipboard()
    this.graph.cleanHistory()

    this.stencilContainer.appendChild(slider.container)

    const component1 = createGroup('group1', 170, 70, '#eeeeee', 'Requirement')
    const component2 = createNode('node1', 140, 70, 'Table', 'Filter', 'Columns', this.graph)
    const component3 = createReuseNode('reusenode', 170, 49, 'REUSE', 'Requirement', 'Columns', this.graph)
    slider.load([component1, component2, component3], 'group1')
  }

  refContainer = (container: HTMLDivElement) => {
    this.container = container
  }
  refStencil = (container: HTMLDivElement) => {
    this.stencilContainer = container
  }
  onClickUndo = () => {
    this.graph.undo()
  }
  onClickRedo = () => {
    this.graph.redo()
  }
  onClickZoomIn = () => {
    this.graph.zoom(0.1)
  }
  onClickZoomOut = () => {
    this.graph.zoom(-0.1)
  }
  onClickFront = () => {
    const cell = getSelectedCell(this.graph)
    if (cell !== null) {
      cell.toFront()
    }
  }
  onClickBack = () => {
    const cell = getSelectedCell(this.graph)
    if (cell !== null) {
      cell.toBack()
    }
  }

  onClickDelete = () => {
    this.child.setState({ editType: 'GRAPH' })
    const cells = this.graph.getSelectedCells()
    if (cells && cells.length) {
      this.graph.removeCells(cells)
    } else {
      message.error('Please select components firstly')
    }
  }
  private copyCells: Cell[]
  onClickCopy = () => {
    const cells = this.graph.getSelectedCells()
    if (cells && cells.length) {
      for (var i = 0; i < cells.length; i++) {
        if (cells[i] instanceof Edge) {
          message.error('Edges cannot be copied!')
          return
        }
      }
      this.graph.copy(cells)
      this.copyCells = cells
      message.info('Component copied')
    } else {
      message.error('Please select components firstly')
    }
  }
  onClickPaste = () => {
    if (this.graph.isClipboardEmpty()) {
      message.error('Clipboard is empty!')
    } else {
      let curY = -9999999
      this.graph.getCells().forEach(element => {
        if (element instanceof Node) {
          const node = (element as Node)
          const y = node.getPosition().y + node.getSize().height
          if (y > curY) {
            curY = y
          }
        }
      });
      let curYa = 9999999
      this.copyCells.forEach(element => {
        if (element instanceof Node) {
          const node = (element as Node)
          const y = node.getPosition().y
          if (y < curYa) {
            curYa = y
          }
        }
      });

      const cells = this.graph.paste({ offset: { dx: 0, dy: curY - curYa + 20 } })
      let i = 0
      for (i = 0; i < this.copyCells.length; i++) {
        cells[i].setAttrs(this.copyCells[i].attrs)
        // if (cells[i] instanceof Group) {
        //   autoJustSizeOfSubRequirement(cells[i] as Group)
        // } else {
        //   autoJustSizeOfSubQuery(cells[i] as Node)
        // }
      }

      this.graph.cleanSelection()
      this.graph.select(cells)
    }
  }
  onClickAdjust = () => {
    const cells = this.graph.getSelectedCells()
    if (cells && cells.length === 1) {
      const node = cells[0]
      if (!(node instanceof Group)) {
        message.error('Please select a Requirements Module!')
        return
      }

      const childs: Cell[] = []
      const getChilds = (node: Cell) => {
        const temp = node.getChildren()
        temp?.forEach(test => {
          childs.push(test)
          getChilds(test)
        })
      }
      getChilds(node)
      if (childs.length > 0) {
        let newTopLeftX = 99999999
        let newTopLeftY = 99999999
        let newBottomRightX = -99999999
        let newBottomRightY = -99999999
        childs.forEach(element => {
          const temp = (element as Node)
          const test = temp.getBBox().inflate(10)
          if (test.topLeft.x < newTopLeftX) newTopLeftX = test.topLeft.x
          if (test.topLeft.y < newTopLeftY) newTopLeftY = test.topLeft.y - 20
          if (test.bottomRight.x > newBottomRightX) newBottomRightX = test.bottomRight.x
          if (test.bottomRight.y > newBottomRightY) newBottomRightY = test.bottomRight.y
        });
        node.setPosition(newTopLeftX, newTopLeftY)
        node.setSize(newBottomRightX - newTopLeftX, newBottomRightY - newTopLeftY)
        autoJustSizeOfSubRequirement(node)
      } else {
        autoJustSizeOfSubRequirement(node)
      }
    } else {
      message.info('Please select a Requirements Module')
    }
  }

  onClickToJPG = () => {
    const vb = this.graph.getContentBBox()
    const vbr = Rectangle.create(vb.x, vb.y, vb.width, vb.height)

    this.graph.toSVG((dataUri: string) => {
      // 
      DataUri.downloadDataUri(DataUri.svgToDataUrl(dataUri), 'VQFlow_Graph.svg')
    }, {
      copyStyles: true,
      viewBox: vbr,
    })
  }
  onClickToJSON = () => {
    const blob = new Blob([JSON.stringify(this.graph.toJSON())], { type: 'text/plain' })
    DataUri.downloadBlob(blob, 'VQFlow_Graph.json')
    message.info(JSON.stringify('Saved', null, 2))
  }
  onClickCenter = () => {
    this.graph.zoomToFit({ padding: 10, maxScale: 1 })
    this.graph.centerContent()
  }

  onClickGetClockwisePortOfSourceNode = () => {
    const cell = getSelectedCell(this.graph)
    if (cell !== null) {
      if (cell instanceof Edge) {
        let edge = cell
        const sourceNode = edge.getSourceNode()
        const sourcePortId = edge.getSourcePortId()
        const targetPortId = getPortIdClockwise(sourcePortId as string)
        edge.setSource({ cell: sourceNode!.id, port: targetPortId })
      } else {
        message.error('Please select one edge firstly')
      }
    }
  }
  onClickGetClockwisePortOfTargetNode = () => {
    const cell = getSelectedCell(this.graph)
    if (cell !== null) {
      if (cell instanceof Edge) {
        let edge = cell
        const sourceNode = edge.getTargetNode()
        const sourcePortId = edge.getTargetPortId()
        const targetPortId = getPortIdClockwise(sourcePortId as string)
        edge.setTarget({ cell: sourceNode!.id, port: targetPortId })
      } else {
        message.error('Please select one edge firstly')
      }
    }
  }
  onClickGetAntiClockwisePortOfSourceNode = () => {
    const cell = getSelectedCell(this.graph)
    if (cell !== null) {
      if (cell instanceof Edge) {
        let edge = cell
        const sourceNode = edge.getSourceNode()
        const sourcePortId = edge.getSourcePortId()
        const targetPortId = getPortIdAnticlockwise(sourcePortId as string)
        edge.setSource({ cell: sourceNode!.id, port: targetPortId })
      } else {
        message.error('Please select one edge firstly')
      }
    }
  }
  onClickGetAntiClockwisePortOfTargetNode = () => {
    const cell = getSelectedCell(this.graph)
    if (cell !== null) {
      if (cell instanceof Edge) {
        let edge = cell
        const sourceNode = edge.getTargetNode()
        const sourcePortId = edge.getTargetPortId()
        const targetPortId = getPortIdAnticlockwise(sourcePortId as string)
        edge.setTarget({ cell: sourceNode!.id, port: targetPortId })
      } else {
        message.error('Please select one edge firstly')
      }
    }
  }
  bindChild = (c: ConfigPanel) => {
    this.child = c
  }

  render() {
    (document.getElementById("root") as HTMLDivElement).style.height = window.innerHeight + 'px'

    return (
      <div className="wrap">
        <div className="content" >
          <div id="stencil" className="sider" ref={this.refStencil} />
          <div className="panel">
            <div className="toolbar">
              <Toolbar
                hoverEffect={true}
                size="big" >
                <ToolGroup>
                  <Item
                    name="Copy"
                    tooltip="Copy"// (Meta+C / Ctrl+C)"
                    icon={<CopyOutlined />}
                    onClick={this.onClickCopy}
                  />
                  <Item
                    name="Paste"
                    tooltip="Paste" // (Meta+V / Ctrl+V)"
                    icon={<SnippetsOutlined />}
                    onClick={this.onClickPaste}
                  />
                  <Item
                    name="Delete"
                    tooltip="Delete" // (Delete)"
                    icon={<DeleteOutlined />}
                    onClick={this.onClickDelete}
                  />
                </ToolGroup>
                <ToolGroup>
                  <Item
                    name="Undo"
                    tooltip="Undo (Meta+Z / Ctrl+Z)"
                    icon={<UndoOutlined />}
                    onClick={this.onClickUndo}
                  />
                  <Item
                    name="redo"
                    tooltip="Redo (Meta+Shift+Z / Ctrl+Shift+Z)"
                    icon={<RedoOutlined />}
                    onClick={this.onClickRedo}
                  />
                </ToolGroup>
                <ToolGroup>
                  <Item
                    name="Bring to Front"
                    tooltip="Bring to Front" // (Alt+Shift+F)"
                    icon={<VerticalAlignTopOutlined />}
                    onClick={this.onClickFront}
                  />
                  <Item
                    name="Send to Back"
                    tooltip="Send to Back" // (Alt+Shift+B)"
                    icon={<VerticalAlignBottomOutlined />}
                    onClick={this.onClickBack}
                  />
                </ToolGroup>
                <ToolGroup>
                  <Item
                    name="Zoom In"
                    tooltip="Zoom In (Alt+Shift+=)"
                    icon={<ZoomInOutlined />}
                    onClick={this.onClickZoomIn}
                  />
                  <Item
                    name="Zoom Out"
                    tooltip="Zoom Out (Alt+Shift+-)"
                    icon={<ZoomOutOutlined />}
                    onClick={this.onClickZoomOut}
                  />
                </ToolGroup>
                <ToolGroup>
                  <Item
                    name="Center"
                    tooltip="Center (Alt+Shift+C)"
                    icon={<CompressOutlined />}
                    onClick={this.onClickCenter}
                  />
                  <Item
                    name="Adjust Size"
                    tooltip="Adjust the Size" // (Alt+Shift+A)"
                    icon={<ShrinkOutlined />}
                    onClick={this.onClickAdjust}
                  />
                </ToolGroup>
                <ToolGroup>
                  <Item
                    name="Move source port clockwise"
                    tooltip="Move source port clockwise"
                    icon={<SourceClockwiseIcon />}
                    onClick={this.onClickGetClockwisePortOfSourceNode}
                  />
                  <Item
                    name="Move source port anticlockwise"
                    tooltip="Move source port anticlockwise"
                    icon={<SourceAntiClockwiseIcon />}
                    onClick={this.onClickGetAntiClockwisePortOfSourceNode}
                  />
                  <Item
                    name="Move target port clockwise"
                    tooltip="Move target port clockwise"
                    icon={<TargetClockwiseIcon />}
                    onClick={this.onClickGetClockwisePortOfTargetNode}
                  />
                  <Item
                    name="Move target port anticlockwise"
                    tooltip="Move target port anticlockwise"
                    icon={<TargetAntiClockwiseIcon />}
                    onClick={this.onClickGetAntiClockwisePortOfTargetNode}
                  />
                </ToolGroup>
                <ToolGroup>
                  <Item
                    name="Save to JSON"
                    tooltip="Save to JSON"
                    icon={<FileOutlined />}
                    onClick={this.onClickToJSON}
                  />
                  <Item
                    name="Save to SVG"
                    tooltip="Save to SVG" // (Alt+Shift+S)"
                    icon={<FileImageOutlined />}
                    onClick={this.onClickToJPG}
                  />
                </ToolGroup>
              </Toolbar>
            </div>
            <div id="container" ref={this.refContainer}></div>
          </div>
          <div className='config'>
            <ConfigPanel onRef={this.bindChild} />
          </div>
        </div>
      </div>
    )
  }
}
