import { Node } from '@antv/x6'
import { Cell } from '@antv/x6'
import { Graph } from '@antv/x6'
import { Group } from '../nodes/Group'
import { message } from 'antd'
import { expand } from './GraphEvents'

export function getSelectedCell(graph: Graph) {
    const cells = graph.getSelectedCells()
    if (cells.length !== 1) {
        message.error('Please select one component!')
        return null
    } else {
        return cells[0]
    }
}
export function autoJustSizeOfSubRequirement(node: Group) {
    const title: string = node.getAttrByPath('label/text')
    const width: number = node.getSize().width
    const length1 = (getLengthOfStr(title) + 6) * 8.4
    const length2 = (width > length1) ? width : length1
    if (node.isCollapsed()) {
        const lines = title.split('\n').length
        const curHeight = (lines + 1) * 14
        node.setSize({ width: length2, height: curHeight })
    } else {
        node.setSize({ width: length2, height: node.getSize().height })
    }
}
export function getLengthOfStr(str: string) {
    let result = 0
    const splits = str.split('\n')
    splits.forEach(element => {
        if (element.length > result) result = element.length
    });
    return result
}
export function autoJustSizeOfSubQuery(node: Node) {
    if (node instanceof Group) {
        message.error('Please select a Mini-query component!')
        return
    }
    const title: string = node.getAttrByPath('rank/text')
    const content: string = node.getAttrByPath('name/text')
    const show: string = node.getAttrByPath('show/text')
    const length1 = (getLengthOfStr(title) + 6) * 8.4
    const length2 = (getLengthOfStr(content) + 6) * 8.4
    const length3 = (getLengthOfStr(show) + 6) * 8.4
    const newWidth = Math.max(length1, length2, length3, 150)
    const line1 = title.split('\n').length
    const line2 = content.split('\n').length
    const line3 = show.split('\n').length
    const lines = line1 + line2 + line3
    const lineHeight = 14
    const marginTop = 9 // lineHeight / 2
    const newHeight = lines * lineHeight + marginTop * 4
    node.setAttrByPath('rank/refY', marginTop / newHeight)
    node.setAttrByPath('name/refY', (marginTop * 2 + line1 * lineHeight) / newHeight)
    node.setAttrByPath('show/refY', (marginTop * 3 + (line1 + line2) * lineHeight) / newHeight)

    node.setSize({ width: newWidth, height: newHeight })
}

export function autoJustSizeOfReuse(node: Node) {
    if (node instanceof Group) {
        message.error('Please select a Reuse component!')
        return
    }
    const content: string = node.getAttrByPath('name/text')
    const length2 = (getLengthOfStr(content) + 6) * 8.4
    const newWidth = Math.max(length2, 190)
    const line2 = content.split('\n').length
    const lines = 1 + line2 
    const lineHeight = 14
    const marginTop = 10 // lineHeight / 2
    const newHeight = lines * lineHeight + marginTop * 3
    node.setAttrByPath('rank/refY', marginTop / newHeight)
    node.setAttrByPath('name/refY', (marginTop * 2 + 1 * lineHeight) / newHeight)

    node.setSize({ width: newWidth, height: newHeight })
}
export function showOrHidePorts(show: boolean) {
    const container = document.getElementById('container')!
    const ports = container.querySelectorAll(
        '.x6-port-body',
    ) as NodeListOf<SVGElement>
    for (let i = 0, len = ports.length; i < len; i = i + 1) {
        ports[i].style.visibility = show ? 'visible' : 'hidden'
    }
}

export function hidePorts(show: boolean) {
    const container = document.getElementById('root')!
    const ports = container.querySelectorAll(
        '.x6-port-body',
    ) as NodeListOf<SVGElement>
    for (let i = 0, len = ports.length; i < len; i = i + 1) {
        ports[i].style.visibility = show ? 'visible' : 'hidden'
    }
}

export function addCellToGroup(x: number, y: number, curCell: Node, graph: Graph) {
    console.log('addpoint: ')
    if (curCell.hasParent()) {
        return
    }
    if (curCell instanceof Node) {
        // if ((curCell as Node).shape === 'reuse-node') {
            // return
        // }
    }

    const childs: Cell[] = []
    const getChilds = (node: Cell) => {
        const temp = node.getChildren()
        temp?.forEach(test => {
            childs.push(test)
            getChilds(test)
        })
    }
    getChilds(curCell)

    const cells = graph.getCells()
    cells.forEach(cell => {
        if (cell !== curCell && (cell instanceof Group)) {
            let isChild = false
            let i = 0
            for (i = 0; i < childs.length; i++) {
                if (childs[i] === cell) {
                    isChild = true
                }
            }
            if (!isChild) {
                const group = (cell as Group)
                const groupPosition = group.getProp<{ x: number; y: number }>('position')
                const groupWidth = group.getSize().width
                const groupHeight = group.getSize().height

                const curPosition = curCell.getProp<{ x: number; y: number }>('position')
                const curWidth = curCell.getSize().width
                const curHeight = curCell.getSize().height
                const x = curPosition.x + curWidth / 2
                const y = curPosition.y + curHeight / 2

                if (x > groupPosition.x && x < groupPosition.x + groupWidth
                    && y > groupPosition.y && y < groupPosition.y + groupHeight) {
                    group.addChild(curCell)
                    group.toBack()
                    expand(curCell as Node)
                    return
                }
            }
        }
    });
}

export function createGroup(
    id: string,
    width: number,
    height: number,
    fill: string,
    title: string,
) {
    const group = new Group({
        id: id,
        width: width,
        height: height,
        attrs: {
            body: {
                fill: fill || 'blue',
                stroke: '#111111',
            },
            rect: {
                fill: fill || 'blue',
                stroke: '#000000',
            },
            label: {
                text: title,
                fill: '#111111',
                fontFamily: 'Courier New',
                fontSize: 14,
            },
        },
    })
    return group
}
export function getPortIdClockwise(sourcePortId: string) {
    switch (sourcePortId) {
        case 'bottom1': return 'left1'
        case 'left1': return 'top1'
        case 'top1': return 'top2'
        case 'top2': return 'top3'
        case 'top3': return 'right1'
        case 'right1': return 'bottom3'
        case 'bottom3': return 'bottom2'
        case 'bottom2': return 'bottom1'
        default:
            break;
    }
    throw new Error('Function not implemented.')
}

export function getPortIdAnticlockwise(sourcePortId: string) {
    switch (sourcePortId) {
        case 'bottom1': return 'bottom2'
        case 'left1': return 'bottom1'
        case 'top1': return 'left1'
        case 'top2': return 'top1'
        case 'top3': return 'top2'
        case 'right1': return 'top3'
        case 'bottom3': return 'right1'
        case 'bottom2': return 'bottom3'
        default:
            break;
    }
    throw new Error('Function not implemented.')
}
export function createNode(
    id: string,
    width: number,
    height: number,
    title: string,
    content: string,
    showText: string,
    graph: Graph,
) {
    return graph.createNode({
        id: id,
        width: width,
        height: height,
        shape: 'subquery-node',
        attrs: {
            rank: {
                text: title,
                wordSpacing: '-1px',
                letterSpacing: 0,
            },
            name: {
                text: content,
                letterSpacing: 0,
            },
            show: {
                text: showText,
                letterSpacing: 0,
            },
        }
    })
}
export function createReuseNode(
    id: string,
    width: number,
    height: number,
    title: string,
    content: string,
    showText: string,
    graph: Graph,
) {
    return graph.createNode({
        id: id,
        width: width,
        height: height,
        shape: 'reuse-node',
        attrs: {
            rank: {
                text: title,
                wordSpacing: '-1px',
                letterSpacing: 0,
            },
            name: {
                text: content,
                letterSpacing: 0,
            },
        }
    })
}
export function createEdge(
    id: string,
    source: string,
    target: string,
    graph: Graph,
    vertices?: { x: number; y: number }[],
) {
    return graph.addEdge({
        id,
        source,
        target,
        vertices,
        label: '',
    })
}
