import React, { } from 'react';
import { Tabs, Input, Divider } from "antd";
import { ConfigPanel } from './ConfigPanel';
import { Node } from '@antv/x6'
import { autoJustSizeOfReuse, getSelectedCell } from '../utils/Util';

export interface Props {
  onRef: (c: ConfigReuse) => void
}

export interface State {
  subqueryText: string
}

const { TabPane } = Tabs;
const { TextArea } = Input

export class ConfigReuse extends React.Component<Props, State> {
  state: State = {
    subqueryText: '',
  }

  public parent: ConfigPanel

  setParent(c: ConfigPanel) {
    this.parent = c
  }

  componentDidMount() {
    this.props.onRef(this)
  }
  componentWillUnmount = () => {
    this.setState = (state,callback)=>{
      return;
    };
}

  onChangeSubQuery = (e: any) => {
    const cell = getSelectedCell(this.parent.parent.graph)
    if (cell !== null) {
      const node = (cell as Node)
      node.setAttrByPath('name/text', e.target.value)
      this.setState({ subqueryText: e.target.value })
      autoJustSizeOfReuse(node)
    }
  }

  render() {
    return (
      <Tabs defaultActiveKey="1">
        <TabPane tab="Reuse Setting" key="1" style={{ padding: 3 }}  >
          <Divider orientation="left" orientationMargin="0">Requirement to reuse</Divider>
          <TextArea rows={10} autoFocus={true} onChange={this.onChangeSubQuery} value={this.state.subqueryText} placeholder="Please copy the requirements description of a Requirements Module"/>
        </TabPane>
      </Tabs>
    );
  }
}